@import '../variables.scss';

main {
  background-color: #F0F0F6;
  min-height: calc(100vh - 105px - $headerHeight);
}

.inner-content {
  max-width: 1320px;
  width: 100%;
  padding: 40px 15px;
  margin: 0 auto;
  @media (min-width: $breakpoint-xl) {
    padding-left: 0;
    padding-right: 0;
  }
}